.app__newsletter{
  padding:2rem 4rem;
    border:1px solid var(--color-golden);
    background:var(--color-black);

}

.app__newsletter_heading{
    text-align:center;
}

.app__newsletter_input{
    flex-direction:row;
    margin-top:3rem;
}

.app__newsletter_input input{
    width:620px;
    border:1px solid var(--color-golden);
    border-radius:5px;
    font-size:1rem;
    font-family:var(--font-base);
    color: var(--color-white);
    margin-right:2rem;
    padding:0.75rem ;
    background: var(--color-black);
}

.app__newsletter_input button{
    width:max-content;
}

@media screen and (min-width:2000px){

    .app__newsletter_input input{
        font-size: 2rem;
        
    }

}

@media screen and (max-width:990px){

    .app__newsletter_input{
        flex-direction: column;
        width:100%;
    }

    .app__newsletter_input input{
        margin: 0 0 2rem 0;
        width:100%
    }

}
@media screen and (max-width:650px){

    .app__newsletter{
        padding:2rem 0;
        border:none;
    }

}
@media screen and (min-width:300px){
.app__newsletter_heading h1{
    font-size: 32px;
    line-height: 50px;
}
}

