.app__aboutus{
    position:relative;
}

.app__aboutus_overlay{
    position:absolute;
    inset:0;
}
.app__aboutus_overlay img{
    width:391px;
    height:415px;
    z-index: 0;
}

.app__aboutus_content{
    width: 100%;
    z-index:2;
}
.app__aboutus_content_about{
    flex:1;
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    flex-direction: column;
    text-align: right;
}
.app__aboutus_content_knife{
    margin:2rem 4rem;
}
.app__aboutus_content_knife img{
    height:910px;
}
.app__aboutus_content_history{
    flex:1;
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    flex-direction: column;
    text-align: left;
}

.app__aboutus_content_history p,.app__aboutus_content_about p{
    margin:2rem 0 rem;
    color: var(--color-grey);
}

@media screen and (max-width:2000px){
    .app__aboutus_content_knife{
        height:1110px;
    }
    .app__aboutus_content_history p,.app__aboutus_content_about p{
        margin:2rem 0 rem;
    
    }

    
}
@media screen and (max-width:900px){
    .app__aboutus_content{
        flex-direction: column;

    }
    .app__aboutus_content_knife{
        margin: 4rem 0;
    }


    
    
}
@media screen and (max-width:650px){

    .app__aboutus_overlay img{
        width:80%;
        height:320px;
    }
    
}